/* Using SCSS variables to store breakpoints */
.blog-post-meta__social-share {
	display: inline-block;
	height: 1.75rem;
	padding: 0.4375rem;
	filter: invert(1);
	width: auto;
}

.blog-header {
	line-height: 1;
	border-bottom: 1px solid #e5e5e5;
}
.blog-header-logo {
	font-family: "Playfair Display", Georgia, "Times New Roman", serif;
	font-size: 2.25rem;
}
.blog-header-logo:hover {
	text-decoration: none;
}
.blog-header__social-icon {
	height: 20px;
	width: auto;
}

.marquee__container {
	margin: 1rem 0;
	padding: 0.5rem;
}
.marquee__container p {
	font-weight: 700;
	margin-bottom: 0;
	text-transform: uppercase;
	font-style: italic;
}

/*
 * Footer
 */
.blog-footer {
	padding: 2.5rem 0;
	text-align: center;
	border-top: 0.05rem solid #e5e5e5;
}

.blog-footer p:last-child {
	margin-bottom: 0;
}

.chapter__share img {
	height: 16px;
	width: auto;
	display: block;
	margin: 8px 0;
}

.depth-1 {
	margin-left: 40px;
}

.depth-2 {
	margin-left: 40px;
}

.depth-3 {
	margin-left: 40px;
}

.depth-4 {
	margin-left: 40px;
}

.depth-5 {
	margin-left: 40px;
}

.depth-6 {
	margin-left: 40px;
}

.depth-7 {
	margin-left: 40px;
}

